import { Box, Container, Typography } from "@mui/material";
import React from "react";
import "./howItWork.css";
import workImg from "../../../../assets/images/howitwork_bg.jpg";
import ruduLogo from "../../../../assets/images/howitwork_center.png";
import playImg from "../../../../assets/images/howitwork_play.png";

const HowItWork = () => {
  return (
    <Container className="howItWork">
      <Box mt={3}>
        <Typography
          variant="h5"
          fontFamily={"Segoe UI"}
          textAlign={"center"}
          fontSize={"22px"}
          fontWeight={"600"}
          color={"#000"}
        >
          HOW IT WORKS
        </Typography>
      </Box>
      <Box className="workImg">
        <img src={workImg} alt="workImg" className="workImgFluid" />

        <div className="ruduLogo">
          <img src={ruduLogo} alt="ruduLogo" className="ruduImg" />
        </div>
        {/* <div className="play">
          <img src={playImg} alt="playImg" className="playImg" />
        </div> */}
      </Box>
    </Container>
  );
};

export default HowItWork;
