import React from 'react'
import Banner from '../../components/banner/banner'
import banner from "../../../assets/images/image4.jpg";
import img1 from "../../../assets/images/solutionsec4-1.png";
import img2 from "../../../assets/images/Rectangle 35.png";
import SolutionComponent from './SolutionComponent';
import SharedPagination from "./CustomPagination";
const SolutionSection4 = () => {
    return (
        <div style={{marginTop:"64px"}}>
            <div style={{ width: "100%" }}>
                <img
                    src={banner}
                    alt="banner"
                    className="img-fluid"
                    style={{
                        width: "100%",
                        height: "400px",
                        objectFit: "cover",
                    }}
                />
            </div>
            <div>
                <Banner title="" />
            </div>


            <div id='solutionsection3 container-fluid'>
                <div class='container-fluid'>
                    <div style={{ marginTop: "50px" }}>
                        <SolutionComponent
                            title="Insurance Companies are setting for a Profit Gain from the Mobile Telematics Systems "
                            // subtitle=""
                            paragraph1="Mobile telematics systems are having a dramatic impact on the automotive industry. And for good reason: unlike the rather stagnant traditional method of measuring how a driver drives — using generic and dated information associated with a driver’s age, gender, even marital status — usage-based insurance (or UBI) uses a person’s actual driving behavior to determine an accurate and fair insurance premium."
                            paragraph2="It’s an approach that has clients and insurance companies — at least, those who have adopted and implemented coherent mobile telematics programs very excited. In fact, it’s been a huge boon for young drivers, who for years even generations have been unfairly burdened with hefty monthly insurance payments."
                            paragraph3="But it’s not just consumers who are able to benefit from the growing popularity of automotive insurance telematics and usage-based insurance. Insurance companies are also setting themselves up for a profit gain, for several simple reasons:"
                            showSvg={true}
                        />
                    </div>
                    <div style={{ marginTop: "50px" }}>
                        <SolutionComponent
                            title="MOBILE TELEMATICS ALLOWS INSURERS TO LEARN MORE ABOUT THEIR CUSTOMERS"
                            // subtitle=""
                            paragraph1="It used to be that, in the best-case scenario, a client signed on with an insurance provider and quietly went about paying their premiums for years. Their lives changed and, to some extent, the insurance company learned about this and made necessary adjustments to their premium. But it’s not as if the insurance company really understood their clients or had a clear picture of how they drove — unless they faced a torrent of speeding tickets and traffic accidents."
                            paragraph2="But with the introduction of both mobile and device based telematics technology, that’s completely different. By leveraging a smartphone or installing automotive telematics in a vehicle, an automotive insurance provider can, in just a day or two, acquire a pretty accurate picture of a client’s on-road driving behavior. Insurers can see how fast they drive, how quick they take corners, how slowly they brake, when they drive and where they drive."
                            paragraph3="That’s hugely useful information on a client-to-client basis. But it’s also useful when considered in a collective way. By collecting all of the information related to a large pool of clients, insurers can start to understand how drivers behave at certain times of the day, in metropolitan or rural environments, even while driving specific types of vehicles. And that information can be collected and analyzed in real time — not just once or twice a year."
                            showSvg={false}
                        />
                    </div>
                    <div style={{ marginTop: "50px" }}>
                        <SolutionComponent
                            title="MOBILE TELEMATICS IS COST-EFFICIENT "
                            // subtitle=""
                            paragraph1="For the past few years, most insurance companies taking a lead role in the development of automotive telematics and usage-based insurance have used small devices that connect to a vehicle’s OBD-II (or diagnostic) port to monitor driver behavior and craft a more accurate picture of how a client acts on the road. "
                            imageSrc={img1}
                            showSvg={false}
                           
                        />
                    </div>

                    <div style={{ marginTop: "50px" }}>
                        <SolutionComponent
                            paragraph1="For the past few years, most insurance companies taking a lead role in the development of automotive telematics and usage-based insurance have used small devices that connect to a vehicle’s OBD-II (or diagnostic) port to monitor driver behavior and craft a more accurate picture of how a client acts on the road."
                            paragraph2="The rapid emergence of smartphones — no longer an expensive technology reserved for the very rich — has made mobile telematics the new champion of usage-based insurance. Besides the fact that just about everyone has one, smartphones are also a lucrative tool for insurance companies because they contain sensors that give insurance providers access to measurements like distance traveled and speed. They can even help insurance providers determine who’s driving (the owner of the vehicle or someone else), how fast the driver is taking turns and braking, and where and when the trip is taking place."
                            paragraph3="
                            In the end, the smartphone gives insurance providers cheap and easy access to a wealth of accurate data, helping them bring usage-based insurance options to regions and people previously unfamiliar with the technology."
                            showSvg={false}
                        />
                    </div>


                    <div style={{ marginTop: "50px" }}>
                        <SolutionComponent
                            title="MOBILE TELEMATICS SYSTEMS KEEPS INSURERS AHEAD OF THE COMPETITION "
                            // subtitle=""
                            paragraph1="What do you get with an insurance program that uses the technology you already own (a smartphone) and that rewards clients for driving safer? That’s right — a clear, tangible reason to abide by the rules of the road. It’s a system that effectively rewards the road’s safest drivers with lower insurance premiums. At the same time, it lets insurance providers see who really poses a threat to other drivers, allowing them the option to increase those customers’ premiums while lowering those of safer drivers."
                            imageSrc={img2}
                            showSvg={false}
                        
                        />
                    </div>


                    <div style={{ marginTop: "30px" }}>
                        <SolutionComponent
                            paragraph1="In the end, mobile telematics with the rich insight from mobile data analytics lets insurance companies build more competitive pricing and premiums, giving them an advantage over other automotive insurance providers. Additionally, the collection of a wealth of data allows insurance companies that participate in UBI programs the opportunity to see how their pricing structure will build and change over time."
                            showSvg={false}
                        />
                    </div>
                </div>
            </div>


            <div class='container' style={{marginTop:"30px",marginBottom:"50px"}}>
        <div class='row'>
          <div class='col-sm-3'></div>
          <div class='col-sm-6' style={{display:"flex",alignItems:"center",justifyContent:"center"}}>  <SharedPagination /> </div>
          <div class='col-sm-3'></div>
        </div>

      </div>


      </div>
    )
}

export default SolutionSection4