import React from "react";
import "./tnc.css";
import TitleComp from "../components/titleComponent.jsx/headerTitleComp";
import TncParagraph from "./paragraphComponent";
import Banner from "../components/banner/banner";
import bannerimage from "../../assets/images/tc-banner.jpg";




const Cookies = () => {
  return (
    <div style={{ marginTop: "64px" ,marginBottom:"80px"}}>
      <div style={{ width: "100%" }}>
        <img
          src={bannerimage}
          alt="banner"
          className="img-fluid"
          width="100%"
          style={{ height: "400px", objectFit: "cover" }}
        />
      </div>
      <Banner title="" />
      <TitleComp title="COOKIES POLICY" />
      <div>
    
          <TncParagraph/>
 
      </div>
    </div>
  );
};

export default Cookies;
