import React from 'react'
import Banner from '../../components/banner/banner'
import banner from "../../../assets/images/image3.jpg";
import img1 from '../../../assets/images/solutionsection3.jpg'
import SolutionComponent from './SolutionComponent';
import SharedPagination from "./CustomPagination";
const SolutionSection3 = () => {
    return (
        <div style={{marginTop:"64px"}}>
            <div style={{ width: "100%" }}>
                <img
                    src={banner}
                    alt="banner"
                    className="img-fluid"
                    style={{
                        width: "100%",
                        height: "400px",
                        objectFit: "cover",
                    }}
                />
            </div>
            <div>
                <Banner title="" />
            </div>


            <div id='solutionsection3 container-fluid'>
                <div class='container-fluid'>
                    <div style={{ marginTop: "50px" }}>
                        <SolutionComponent
                            title="MOBILE TELEMATICS PROVIDERS ARE COMMONLY SEEN AS DISRUPTIVE INNOVATORS "
                            subtitle=""
                            paragraph1="Say an automotive insurance provider adopts a mobile telematics insurance option while also maintaining the traditional insurance model for many of its other customers. After a year or two only a small but significant percentage of the company’s customers have signed on for the mobile usage-based program — perhaps because the company hasn’t had an opportunity to properly market its UBI offering. Is the usage-based insurance effort a failure?"
                            paragraph2="No, and for one major reason: it shows clients, even those who don’t opt for the mobile UBI model (at this time) that their insurance provider is an innovator and actively developing new ideas and insurance programs that can benefit their clients. Even if a customer determines that mobile telematics isn’t right for them at this time, they may change their mind down the road. And they’ll be happy about their insurance provider and its forward-thinking approach to expanding their product offerings."
                            paragraph3="Then, of course, there are the rewards for customers who do sign up for a mobile telematics program. So long as they drive safe, they’ll see their premiums go down, giving them a feeling that their insurance provider really cares about rewarding them for safe on-road behavior. The relationship that emerges between the insurance provider and the customer will be closer and more intimate, making a safe driver less likely to sever their ties with their provider in favor of another automotive insurance company."
                            imageSrc={img1}
                            showSvg={true}
                            imageheight='500'
                        />
                    </div>
                    <div style={{ marginTop: "50px" }}>
                        <SolutionComponent
                            title="MOBILE TELEMATICS SYSTEMS HELP REDUCE COSTS ASSOCIATED WITH CLAIMS"
                            subtitle=""
                            paragraph1="If you created an automotive insurance program that clearly rewarded drivers for safe driving, what kind of people do you expect would sign up for such a program? The answer: far less risky (and therefore far more profitable) customers. According to one consulting firm that’s researched this issue extensively, on average it can lead to a 57 percent decrease in total claims costs. "
                            paragraph2=" Meanwhile, more risky drivers — or drivers who are more likely to get into a traffic accident or receive a speeding ticket — will be convinced to avoid the usage-based insurance model and may even leave the insurance company, thereby flocking to other insurance companies that offer more traditional (and less profitable) insurance programs."
                            showSvg={true}
                        />
                    </div>
                </div>
            </div>

            <div class='container' style={{marginTop:"30px",marginBottom:"50px"}}>
        <div class='row'>
          <div class='col-sm-3'></div>
          <div class='col-sm-6' style={{display:"flex",alignItems:"center",justifyContent:"center"}}>  <SharedPagination /> </div>
          <div class='col-sm-3'></div>
        </div>

      </div>



        </div>
    )
}

export default SolutionSection3