import React from "react";
import Solutions from "./solutionsUI";
import background from "../../../assets/images/background2.png";
import Points from "./points";
import img5 from "../../../assets/images/i5.png"; // Import your images
import img2 from "../../../assets/images/i2.png"; // Import your images
import img3 from "../../../assets/images/i3.png";
import img4 from "../../../assets/images/i4.png";
import banner from "../../../assets/images/image2.jpg";
import Banner from "../../components/banner/banner";
import { Box, Grid } from "@mui/material";
import SharedPagination from "./CustomPagination";

function SecondPage() {
  const pointsData = [
    {
      imgSrc: img5,
      title: "Connected Insurance",
      points: [
        "Solutions that reduce risk, lower claim costs, improve customer service and increase engagement"
      ],
    },
    {
      imgSrc: img2,
      title: "Innovative Risk Segmentation and Pricing",
      points: [
        "Empowering insurers to use precise data based and controllable variables in their auto insurance rating",
      ],
    },
    {
      imgSrc: img3,
      title: "Reduce claims cost ",
      points: [
        "Access key details like driving risk alerts, anticipation, self confidence, vehicle impact location etc.",

      ],
    },
    {
      imgSrc: img4,
      title: "Improve your book of business",
      points: [
        "Improve your book by identifying, engaging, and rewarding safe drivers",

      ],
    },

    // Add more custom points data as needed
  ];

  return (
    <div style={{marginTop:"64px"}}>
      <div style={{ width: "100%" }}>
        <img
          src={banner}
          alt="banner"
          className="img-fluid"
          style={{
            width: "100%",
            height: "400px",
            objectFit: "cover",
          }}
        />
      </div>
      <div>
        <Banner title="" />
      </div>
      <Solutions
        title="Value for Motor Insurers "
        paragraph=""
        imageSrc={background}
      />
      <Points pointsData={pointsData} /> {/* Pass the pointsData prop */}
     


      <div class='container' style={{marginTop:"0px",marginBottom:"50px"}}>
        <div class='row'>
          <div class='col-sm-3'></div>
          <div class='col-sm-6' style={{display:"flex",alignItems:"center",justifyContent:"center"}}>  <SharedPagination /> </div>
          <div class='col-sm-3'></div>
        </div>

      </div>
    </div>
  );
}

export default SecondPage;
