import React, { useState, useEffect } from "react";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Typography from "@mui/material/Typography";
import {
  Button,
  CardActionArea,
  CardActions,
  ButtonGroup,
} from "@mui/material";
import { Grid } from "@mui/material";
import "./solutions.css";
import Banner from "../components/banner/banner";
import image1 from "../../assets/images/image1.jpg";
import image2 from "../../assets/images/image2.jpg";
import image3 from "../../assets/images/image3.jpg";
import image4 from "../../assets/images/image4.jpg";
import image5 from "../../assets/images/image5.jpg";
import image6 from "../../assets/images/image6.jpg";

import { Link } from "react-router-dom";
import bannerimage from "../../assets/images/solutionsBanner.jpg";

function toCamelCase(str) {
  return str
    .toLowerCase()
    .replace(/[^a-zA-Z0-9]+(. )/g, (m, chr) => chr.toUpperCase());
}

export default function MultiActionAreaCard() {
  useEffect(() => {
    // Scroll to the top when the component mounts
    window.scrollTo(0, 0);

    // Optionally, you can also scroll to the top when a link is clicked
    const scrollToTop = () => {
      window.scrollTo(0, 0);
    };

    // Attach the scroll to top function to all links with class "scroll-to-top"
    const links = document.querySelectorAll(".scroll-to-top");
    links.forEach((link) => {
      link.addEventListener("click", scrollToTop);
    });

    // Clean up event listeners when the component unmounts
    return () => {
      links.forEach((link) => {
        link.removeEventListener("click", scrollToTop);
      });
    };
  }, []);

  const contentData = [
    {
      src: image1,
      text: "Value for Fleet Management",
      buttonText: "BLOG",
      link: "/value_for_fleet_management",
    },
    {
      src: image2,
      text: "Value for Motor Insurers",
      buttonText: "CASE STUDY",
      link: "/value_for_motor_insurers",
    },
    {
      src: image3,
      text: "Mobile Telematics Providers",
      buttonText: "WHITE PAPER",
      link: "/mobile_telematics_providers_are_commonly_seen_as_disruptive_innovators",
    },
    {
      src: image4,
      text: "Insurance and Mobile Telematics",
      buttonText: "BLOG",
      link: "/insurance_companies_are_setting_for_a_profit_gain_from_the_mobile_telematics_systems",
    },
    {
      src: image5,
      text: "Connected Car",
      buttonText: "CASE STUDY",
      link: "/connected_car",
    },
    {
      src: image6,
      text: "What does it mean to drive well",
      buttonText: "WHITE PAPER",

      link: "/what_does_it_mean_to_drive_well_outside_of_any_emergency_situation?",
    },
  ];
  const categories = ["All", "CASE STUDY", "WHITE PAPER", "BLOG"];

  const itemsPerPage = 9;

  const [filteredData, setFilteredData] = useState(contentData);
  const [activeFilter, setActiveFilter] = useState("All");
  const [currentPage, setCurrentPage] = useState(1);

  const handleFilter = (filter) => {
    setActiveFilter(filter);
    if (filter === "All") {
      setFilteredData(contentData);
    } else {
      const filteredContent = contentData.filter(
        (content) => content.buttonText === filter
      );
      setFilteredData(filteredContent);
    }
    setCurrentPage(1);
  };

  const startIndex = (currentPage - 1) * itemsPerPage;
  const endIndex = startIndex + itemsPerPage;
  const displayedData = filteredData.slice(startIndex, endIndex);

  const [windowWidth, setWindowWidth] = useState(window.innerWidth);

  useEffect(() => {
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    };

    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  return (
    <>
      <div style={{ width: "100%", marginTop: "64px" }}>
        <img
          src={bannerimage}
          alt="banner"
          className="img-fluid"
          width="100%"
          style={{ height: "400px" }}
        />
      </div>
      <div>
        <Banner title="" />
      </div>

    
        <div className="solutions-container">
          <Grid container mt={4} mb={4}>
            <Grid item xs={12} sm={12} md={12}>
              <Typography
                variant="h4"
                component="h1"
                className="solutions-title"
              >
                <ButtonGroup
                  variant="text"
                  aria-label="text button group"
                  style={{ height: "15px" }}
                >
                  {categories.map((category, index) => (
                    <Button
                      key={category}
                      onClick={() => handleFilter(category)}
                      className={`filter-option ${
                        activeFilter === category ? "active-filter" : ""
                      }`}
                      style={{
                        fontSize: "18px",
                        textTransform: "capitalize",
                        color: activeFilter === category ? "orange" : "black",
                        fontWeight: activeFilter === category ? "bold" : "normal",
                        padding: "15px",
                        borderRight:
                          index === categories.length - 1
                            ? "none"
                            : "2px solid black",
                      }}
                    >
                      {toCamelCase(category)}
                    </Button>
                  ))}
                </ButtonGroup>
              </Typography>
            </Grid>
          </Grid>
        </div>

        <div
          style={{
            display: "flex",
            justifyContent: "center",
            marginBottom: "80px",
            padding:"20px"
          }}
        >
          <Grid container spacing={{ xs: 1, sm: 2, md: 3 }}>
            {displayedData.map((content, index) => (
              <Grid item xs={12} sm={6} md={4} lg={3} key={index}>
                <Link
                  to={content.link}
                  style={{ textDecoration: "none" }}
                  className="scroll-to-top"
                >
                  <Card
                    sx={{
                      borderRadius: "10px",
                      border: "1px solid #e0e0e0",
                      boxShadow: "0px 4px 10px rgba(0, 0, 0, 0.1)",
                      height: "350px",
                    }}
                  >
                    <CardActionArea>
                      <img
                        src={content.src}
                        alt={`Img ${index + 1}`}
                        style={{
                          width: "100%",
                          height: "222px",
                          objectFit: "cover",
                        }}
                      />
                      <CardContent>
                        <CardActions
                          style={{
                            display: "flex",
                            justifyContent: "space-between",
                            padding: "0px",
                          }}
                        >
                          <Button
                            size="small"
                            className="button"
                            style={{
                              backgroundColor: "#FFCF24",
                              color: "#000000",
                              fontFamily: "Segoe UI",
                              fontWeight: "650",
                              fontSize: "0.8rem",
                              lineHeight: "28px",
                              alignContent: "center",
                              marginBottom: "10px",
                            }}
                          >
                            {content.buttonText}
                          </Button>

                          <p
                            style={{
                              fontFamily: "Segoe UI",
                              fontSize: "14px",
                              fontWeight: "400",
                              color: "#545252",
                              lineHeight: "28px",
                              alignContent: "center",
                            }}
                          >
                            {content.date}
                          </p>
                        </CardActions>
                        <Typography
                          variant="body2"
                          sx={{
                            color: "#000000",
                            fontFamily: "Segoe UI",
                            fontSize: { xl: "18px" },
                            fontWeight: "600",
                            marginTop: "10px",
                          }}
                        >
                          {content.text}
                        </Typography>
                      </CardContent>
                    </CardActionArea>
                  </Card>
                </Link>
              </Grid>
            ))}
          </Grid>
        </div>

        {/* pagination */}
        {/* <div className="solutions-pagination">
          <Stack spacing={2} alignItems={"center"} mt={7} mb={7}>
            <Pagination
              count={pageCount}
              size={windowWidth <= 320 ? "small" : "large"}
              page={currentPage}
              onChange={(event, value) => setCurrentPage(value)}
              classes={{
                root: "custom-pagination-root",
                ul: "custom-pagination-ul",
              }}
              previousIconButtonProps={{ className: "custom-pagination-arrow" }}
              nextIconButtonProps={{ className: "custom-pagination-arrow" }}
              boundaryCount={0}
            />
          </Stack>
        </div> */}
    
    </>
  );
}
