import React from 'react'
import Banner from '../../components/banner/banner'
import banner from "../../../assets/images/image6.jpg";
import SolutionComponent from './SolutionComponent';
import SharedPagination from "./CustomPagination";
const SolutionSection4 = () => {
    return (
        <div style={{marginTop:"64px"}}>
            <div style={{ width: "100%" }}>
                <img
                    src={banner}
                    alt="banner"
                    className="img-fluid"
                    style={{
                        width: "100%",
                        height: "400px",
                        objectFit: "cover",
                    }}
                />
            </div>
            <div>
                <Banner title="" />
            </div>


            <div id='solutionsection3 container-fluid'>
                <div class='container-fluid'>
                    <div style={{ marginTop: "50px" }}>
                        <SolutionComponent
                            title="“What does it mean to drive well, outside of any emergency situation?”"
                            subtitle=""
                            paragraph1="This question was initially asked by insurers and driving psychologists, and it is now coming to cars/trucks/2wheelers manufacturers to offer non-emergency driving assistance systems."
                            paragraph2="The idea of reformulating this question from perspective of prudence and have chosen to estimate in real time level of caution (or prudence) of a driving behavior regarding context. Notion of context is essential and is used to define prudence as ‘the adequacy of the behavior to the context, without necessarily knowing the whole context or all the possible consequences of a behavior’. "
                            paragraph3="The approach seems to be more operational than that consisting in seeking to know the whole context and knowing how to predict consequences of any action, an approach which is somewhat the philosophy of a lot of work on autonomous vehicle."
                            paragraph4="Unfortunately, this last solution obviously leads to vehicles overloaded with sensors and to road infrastructures that are overpriced (since we have to know everything), and to increasing on-board computing power (since we have to understand everything), putting the fruits of this work out of the market because it is too expensive."
                            paragraph5="Such an approach can only be applied to public transport systems, since the purchase or rental of such a vehicle is not an option. It is still necessary that the flow of people transported make it possible to make investment profitable over an acceptable period, which is not yet the case to date."
                            showSvg={true}
                        />
                    </div>
                    <div style={{ marginTop: "50px" }}>
                        <SolutionComponent
                            title="Detection of entry into risk triangle, and action for a rapid exit: “undo unsafe act”"
                            subtitle=""
                            paragraph1="Continuous monitoring of the value of risk so as never to enter, when possible into risk triangle, to do this, we define a maximum authorized risk (lower than the high risk which corresponds to entering triangle. The risk is referred to as ‘risk target’. If we manage not to exceed risk target, then we don’t enter into risk triangle and we do not commit an unsafe act. This is the second level of anticipation."
                            paragraph2="Note: Conditions on real roads may be complex, and sometimes it is not possible to avoid entering triangle, for example, vehicle should slow down to stay prudent but a crazy vehicle appeared behind and may hit rear of the car if braking is done at right level, this idea is to be aware of it and if obliged to enter triangle, then find a quick way to exit."
                            showSvg={false}
                        />
                    </div>

                </div>
            </div>
            <div class='container' style={{marginTop:"30px",marginBottom:"50px"}}>
        <div class='row'>
          <div class='col-sm-3'></div>
          <div class='col-sm-6' style={{display:"flex",alignItems:"center",justifyContent:"center"}}>  <SharedPagination /> </div>
          <div class='col-sm-3'></div>
        </div>

      </div>




      </div>
    )
}

export default SolutionSection4