
import React from "react";
import { Container, Grid } from "@mui/material";
import PointsSection from "./PointsSection";
import DetailBox from "./DetailBox";

export default function Points({ pointsData }) {
  return (
    <Container>
      <Grid xs={12}>
        <Grid container>
          {pointsData.slice(0, 2).map((point, index) => (
            <PointsSection key={index} {...point} />
          ))}
          <DetailBox />
          {pointsData.slice(2).map((point, index) => (
            <PointsSection key={index + 2} {...point} />
          ))}
        </Grid>
      </Grid>
    </Container>
  );
}
