import React from 'react';
import isoimg from '../../assets/images/iso.png';

const IsoCertification = () => {
    return (
        <>
            <div id='isosection' style={{ marginTop: '100px', marginBottom: "100px",marginLeft:"22px",marginRight:"22px" }}>
                <div className='container-fluid main' style={{
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    position: 'relative',
                }}>
                    <div
                        className='container sub'

                    >
                        <div className='row'>
                            <div className='col-sm-10' >
                                <h1>Motiv AI is ISO 27001 Certified</h1>
                            </div>
                            <div
                                className='col-sm-2 isoimg'
                            >
                                <img src={isoimg} alt='Iso Certification' height='250px' />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
};

export default IsoCertification;
