import * as React from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import CssBaseline from "@mui/material/CssBaseline";
import Drawer from "@mui/material/Drawer";
import Grid from "@mui/material/Grid";
import IconButton from "@mui/material/IconButton";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemText from "@mui/material/ListItemText";
import MenuIcon from "@mui/icons-material/Menu";
import Toolbar from "@mui/material/Toolbar";
import Button from "@mui/material/Button";
import Logo from "../../../assets/images/Footerlogo.svg";
import "./header.css";
import { Link, useLocation } from "react-router-dom";
import btnimg from "../../../assets/images/btnimage.png";

const drawerWidth = 240;

export default function DrawerAppBar({ routes }) {
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const location = useLocation(); // Get the current location
  const currentPath = location.pathname;




  const scrollToTop = () => {
    window.scrollTo(0, 0); // Scroll to the top of the page
  };


  const handleButtonClick = () => {
    // Replace 'your_link_url_here' with the actual URL you want to open.
    const linkUrl = "https://dev-rudu-beta.motivai.tech/login ";

    // Open the link in a new window or tab.
    window.open(linkUrl, "_blank");
  };

  const handleDrawerToggle = () => {
    setMobileOpen((prevState) => !prevState);
  };

  const drawer = (
    <Box>
      <List>
        {routes.map((item) => (
          <ListItem key={item.label} disablePadding>
            <ListItemButton
              className={`list-item-button ${
                currentPath === item.path ? "active" : ""
              }`}
              onClick={handleDrawerToggle}
              component={Link}
              to={item.path}
            >
              <ListItemText primary={item.label} />
            </ListItemButton>
          </ListItem>
        ))}
        <Box display="flex" justifyContent={"center"}>
          <Button
            className="custom-button1"
            sx={{
              color: "black",
              backgroundColor: "orange",
              fontWeight: "500",
              display: "flex",
              alignItems: "center",
              fontWeight: "600",
              borderRadius:'40px',
              padding:"15px",
              marginTop:"10px"
            }}
            onClick={handleButtonClick}
          >
            <img
              src={btnimg} // Replace with the URL or path to your image
              alt="Login Icon"
              style={{
                marginRight: "8px", // Adjust the margin as needed for spacing
                width: "24px", // Adjust the width as needed for the image size
                height: "24px", // Adjust the height as needed for the image size
              }}
            />
            Login To Motiv AI
          </Button>
        </Box>
      </List>
    </Box>
  );

  return (
    <Box sx={{ display: "flex" }}>
      <CssBaseline />
      <AppBar >
        <Toolbar
          sx={{
            backgroundColor: "white",
          padding:{xs:'12px',md:"0 1rem",lg:"0 1rem"}
          }}
        >
          <Grid container className="sm-navbar">
         
      <Grid item sm={3} md={4} lg={2} display="flex" style={{ justifyContent: "center" }}>
      <Link to="/" onClick={scrollToTop}>
        <img
          src={Logo}
          alt="Logo"
          style={{ height: "40px", marginRight: "16px" }}
        />
          </Link>
 
      </Grid>
             <Grid item sm={6} md={8} lg={10}>
              <Box
                sx={{
                  display: { xs: "none", md: "flex" },
                }}
              >
                <Grid
                  container
                  spacing={{
                    md: 3,
                    lg: 5,
                    xl: 6,
                  }}
                  sx={{
                    display: { lg: "flex" },
                    justifyContent: { lg: "flex-end" },
                    marginRight:{lg:'50px'}
                  }}
                >
                  {routes.map((item) => (
                    <Grid item key={item.label}>
                    <Button
                      sx={{
                        color: "black",
                        fontWeight:"bold"
                      }}
                      className={`list-item-button ${
                        currentPath === item.path ? "active" : ""
                      }`}
                      component={Link}
                      to={item.path}
                      onClick={scrollToTop} // Add onClick to scroll to top
                    >
                      {item.label}
                    </Button>
                  </Grid>
                  ))}
                </Grid>
               
              </Box>
            </Grid>

            <Grid item>
              <IconButton
                color="inherit"
                aria-label="open drawer"
                edge="start"
                onClick={handleDrawerToggle}
                sx={{ display: { md: "none" },color:"black" }}
              >
                <MenuIcon />
              </IconButton>
            </Grid>
          </Grid>
        </Toolbar>
      </AppBar>
      <Box component="nav">
        <Drawer
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
          sx={{
            display: { sm: "block", md: "none" },
            "& .MuiDrawer-paper": {
              boxSizing: "border-box",
              width: drawerWidth,
            },
          }}
        >
          {drawer}
        </Drawer>
      </Box>
    </Box>
  );
}
