import React from 'react'
import img1 from '../../assets/images/Group 7234.jpg'
import img2 from '../../assets/images/security 1.jpg'
import img3 from '../../assets/images/mobility 1.jpg'
import img4 from '../../assets/images/Vector.jpg'
import img5 from '../../assets/images/car (1) 1.jpg'
import Grid from '@mui/material/Grid';
const HomeSection3 = () => {
    return (
        <>

            <div id='section3' style={{marginTop:"70px"}}>
                <div class='container-fluid main'>

                    <div class='row sub1'>

                        <div class='col-sm-12 content1'><span class='line'></span></div>
                        <div class='col-sm-12 content2' style={{marginTop:"40px"}}><p>POWERED BY MOTIV AI</p></div>
                        <div class='col-sm-12 content3'>
                            <div class='row'>
                                <div class='col-sm-2'></div>
                                <div class='col-sm-8'> <p>‘If I am bad on certain criteria and good on others, it is possible that I am average overall’</p> </div>
                                <div class='col-sm-2'></div>
                            </div>
                        </div>

                    </div>


                    <div class='row sub2'>
                        <div class='col-sm-12 content1'>
                            <img src={img1} class='img-fluid' height='272' />
                        </div>
                    </div>


                    <div class='row sub3'>
                        <div class='col-sm-12 content1'>
                            <div class='row'>
                                <div class='col-sm-1'></div>
                                <div class='col-sm-10'> <p>'Unfortunately; what some drivers do, they commit incautious acts but do not have an accident or even a bad surprise, and they take on bad habits in terms of road safety’</p> </div>
                                <div class='col-sm-1'></div>
                            </div>
                        </div>

                    </div>



                    <div className='container sub4'  >
                        <Grid container spacing={10}>
                            <Grid item xs={12} sm={6} >
                                <Grid container spacing={2}> 
                                    <Grid item xs={2}>
                                        <img src={img2} alt='Image 2' className='img-fluid' />
                                    </Grid>
                                    <Grid item xs={10}>
                                        <div style={{ display: 'flex', flexDirection: 'column' }}>
                                            <div class='head'>Insurance</div>
                                            <div class='content'>
                                                Digital personal and commercial lines solutions enabling more precise underwriting and optimised pricing.
                                            </div>
                                        </div>
                                    </Grid>
                                </Grid>
                            </Grid>

                            <Grid item xs={12} sm={6}>
                                <Grid container spacing={2}>
                                    <Grid item xs={2}>
                                        <img src={img3} alt='Image 3' className='img-fluid' />
                                    </Grid>
                                    <Grid item xs={10}>
                                        <div style={{ display: 'flex', flexDirection: 'column' }}>
                                            <div class='head'>New mobility</div>
                                            <div class='content'>
                                                Pattern profiling for greater risk control. Usage base solutions for extended flexibility and revenue.
                                            </div>
                                        </div>
                                    </Grid>
                                </Grid>
                            </Grid>

                            <Grid item xs={12} sm={6}>
                                <Grid container spacing={2}>
                                    <Grid item xs={2}>
                                        <img src={img4} alt='Image 4' className='img-fluid' />
                                    </Grid>
                                    <Grid item xs={10}>
                                        <div style={{ display: 'flex', flexDirection: 'column' }}>
                                            <div class='head'>Fleet</div>
                                            <div class='content'>
                                                Proactive risk management for incident prevention, energy consumption and CO2 emission control.
                                            </div>
                                        </div>
                                    </Grid>
                                </Grid>
                            </Grid>

                            <Grid item xs={12} sm={6}>
                                <Grid container spacing={2}>
                                    <Grid item xs={2}>
                                        <img src={img5} alt='Image 5' className='img-fluid' />
                                    </Grid> 
                                    <Grid item xs={10}>
                                        <div style={{ display: 'flex', flexDirection: 'column' }}>
                                            <div class='head'> Automotive</div>
                                            <div class='content'>
                                            Tailored subscription-based models and loyalty programs with instant profiling on crash risk, CO2 and EV impact.
                                            </div>
                                        </div>
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Grid>

                    </div>


                </div>
            </div>
        </>
    )
}

export default HomeSection3