import React from "react";
import {Grid, Typography, Container} from "@mui/material";
import img1 from "../../../assets/images/Vector.png";
import img2 from "../../../assets/images/Vector 2.png";
import img3 from "../../../assets/images/Vector 3.png";
import "./solutions.css";

export default function DetailBox() {
  return (
    <Container>
      <Grid container xs={12} md={12} sm={12} xl={12} className="detailbox">
        <Grid item xs={12} sm={6} md={4} className="centered-grid-item">
          <div className="centered-content">
            <img src={img1} alt="" className="centered-image" />
            <Typography variant="h6" className="detail_head" gutterBottom>
              Road Network Context
            </Typography>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="auto"
              height="2"
              viewBox="0 0 320 2"
              fill="none"
            >
              <path d="M0.644043 1H319.395" stroke="#FCAD0D" />
            </svg>
            <Typography variant="body2" className="detail_desc">
              Road type, road familiarity, rural / urban, regions, speed zones,
              etc.
            </Typography>
          </div>
        </Grid>
        <Grid item xs={12} sm={6} md={4} className="centered-grid-item">
          <div className="centered-content">
            <img src={img2} alt="" className="centered-image" />
            <Typography variant="h6" className="detail_head" gutterBottom>
              Driving Dynamics Context
            </Typography>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="auto"
              height="2"
              viewBox="0 0 320 2"
              fill="none"
            >
              <path d="M0.644043 1H319.395" stroke="#FCAD0D" />
            </svg>
            <Typography variant="body2" className="detail_desc">
              Speed, acceleration, braking, cornering, risk against road
              attributes, reckless events.
            </Typography>
          </div>
        </Grid>
        <Grid item xs={12} sm={6} md={4} className="centered-grid-item">
          <div className="centered-content">
            <img src={img3} alt="" className="centered-image" />
            <Typography variant="h6" className="detail_head" gutterBottom>
              Driving Condition Context
            </Typography>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="auto"
              height="2"
              viewBox="0 0 320 2"
              fill="none"
            >
              <path d="M0.644043 1H319.395" stroke="#FCAD0D" />
            </svg>
            <Typography variant="body2" className="detail_desc">
              Day / night, rush hours, weekday/weekend, trip duration.
            </Typography>
          </div>
        </Grid>
      </Grid>
    </Container>
  );
}
