import { Container, Grid, Typography } from "@mui/material";
import React from "react";
import "./solutions.css";


function SolutionComponent(props) {
  return (
    <>
      <Container>
        <Grid>
          <div className="heading">
            {props.showSvg && (
              <div className="line"></div>
            )}
            <Typography className="title" variant="h5" style={{ textTransform: "uppercase" }} >{props.title}</Typography>
            {props.subtitle && props.subtitle !== "" ? (
              <Typography className="title2">
                {props.subtitle}
              </Typography>
            ) : null}
          </div>

          <Typography className="paragraph" style={{ marginTop: "20px" }}>{props.paragraph1}</Typography>
          <Typography className="paragraph" style={{ marginTop: "20px" }}>{props.paragraph2}</Typography>
          <Typography className="paragraph" style={{ marginTop: "20px" }}>{props.paragraph3}</Typography>
          <Typography className="paragraph" style={{ marginTop: "20px" }}>{props.paragraph4}</Typography>
          <Typography className="paragraph" style={{ marginTop: "20px" }}>{props.paragraph5}</Typography>

          <div style={{ marginTop: "30px" }}>
            {props.imageSrc && (
              <div style={{ marginTop: "30px" }}>
                <img
                  src={props.imageSrc}
                  alt="banner"
                  class="img-fluid"
                  style={{
                    width: "100%",
                    height: `${props.imageheight}px`,

                    borderRadius: "20px"
                  }}
                />
              </div>
            )}
          </div>

        </Grid>
      </Container>
    </>
  );
}

export default SolutionComponent;
