import React, { useState, useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom";

const SharedPagination = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const [activePage, setActivePage] = useState(1);
  const totalPages = 6; // Total number of cases in the switch statement

  // Detect changes in the location and set the active page accordingly
  useEffect(() => {
    switch (location.pathname) {
      case "/value_for_fleet_management":
        setActivePage(1);
        break;
      case "/value_for_motor_insurers":
        setActivePage(2);
        break;
      case "/mobile_telematics_providers_are_commonly_seen_as_disruptive_innovators":
        setActivePage(3);
        break;
      case "/insurance_companies_are_setting_for_a_profit_gain_from_the_mobile_telematics_systems":
        setActivePage(4);
        break;
      case "/what_does_it_mean_to_drive_well_outside_of_any_emergency_situation?":
        setActivePage(5);
        break;
      case "/connected_car":
        setActivePage(6);
        break;
      default:
        break;
    }
  }, [location.pathname]);

  const handlePageChange = (page) => {
    setActivePage(page);
    switch (page) {
      case 1:
        navigate("/value_for_fleet_management");
        break;
      case 2:
        navigate("/value_for_motor_insurers");
        break;
      case 3:
        navigate("/mobile_telematics_providers_are_commonly_seen_as_disruptive_innovators");
        break;
      case 4:
        navigate("/insurance_companies_are_setting_for_a_profit_gain_from_the_mobile_telematics_systems");
        break;
      case 5:
        navigate("/what_does_it_mean_to_drive_well_outside_of_any_emergency_situation?");
        break;
      case 6:
        navigate("/connected_car");
        break;
      default:
        break;
    }
    window.scrollTo(0, 0);
  };

  return (
    <div style={{ textAlign: "center", marginTop: "20px" }}>
      {activePage > 1 ? (
        <span
          style={{
            marginRight: "5px",
            cursor: "pointer",
            fontWeight: "bold",
          }}
          onClick={() => handlePageChange(activePage - 1)}
        >
          PREV
        </span>
      ) : (
        <span
          style={{
            marginRight: "5px",
            cursor: "pointer",
            fontWeight: "bold",
            color:"#777",
          }}
        >
          PREV
        </span>
      )}
      {Array.from({ length: totalPages }, (_, index) => (
        <span
          key={index}
          style={{
            marginRight: "5px",
            cursor: "pointer",
            padding: "8px",
            borderRadius: "50%",
            background: activePage === index + 1 ? "#FCAD0D" : "white",
            fontWeight: "bold",
            display: "inline-block",
            border: "1px solid #ccc",
            width: "40px",
          }}
          onClick={() => handlePageChange(index + 1)}
        >
          {index + 1}
        </span>
      ))}
      {activePage < totalPages ? (
        <span
          style={{
            marginLeft: "5px",
            cursor: "pointer",
            fontWeight: "bold",
          }}
          onClick={() => handlePageChange(activePage + 1)}
        >
          NEXT
        </span>
      ) : (
        <span
          style={{
            marginRight: "5px",
            cursor: "pointer",
            fontWeight: "bold",
            color:"#777",
          }}
        >
          NEXT
        </span>
      )}
    </div>
  );
};

export default SharedPagination;
