import React, { useEffect } from "react";
import { BrowserRouter as Router, Route, Routes    } from "react-router-dom";
import DrawerAppBar from "./pages/components/header/header";
import HomeMaster from './pages/home/HomeMaster';
import SoftwareMaster from './pages/software/software'
import MultiActionAreaCard from "./pages/solutions/solutions";
import { Contact } from "./pages/contact/contact";
import Tnc from "./pages/Tnc/tnc";
import Footer from "./pages/components/footer/footer";
import FirstPage from "./pages/solutions/solutionsDetails/firstPage";
import SecondPage from "./pages/solutions/solutionsDetails/secondPage";
import SolutionSection3 from "./pages/solutions/solutionsDetails/SolutionSection3";
import SolutionSection4 from "./pages/solutions/solutionsDetails/SolutionSection4";
import SolutionSection5 from "./pages/solutions/solutionsDetails/SolutionSection5";
import SolutionSection6 from "./pages/solutions/solutionsDetails/SolutionSection6";
import ScrollToTopButton from "./pages/components/scrollTop/ScrollTop";
import Aboutus from "./pages/aboutus/aboutus";
import FAQ from "./pages/Faq/faq";
import Terms from "./pages/terms/terms";  
import Cookies from "./pages/cookies/cookies";
function App() {

  const routes = [
    { label: "Home", path: "/", component: HomeMaster },
    { label: "Software", path: "/software", component: SoftwareMaster },
    { label: "Solutions", path: "/solutions", component: MultiActionAreaCard },
    { label: "Contact", path: "/contact", component: Contact },

    // Remove TNC route from here
  ];
  
  return (
    <Router>
      <DrawerAppBar routes={routes} />
      <Routes>
        {routes.map((route, index) => (
          <Route key={index} path={route.path} element={<route.component />} />
        ))}
        <Route path="/privacy_policy" element={<Tnc /> } />
        <Route path="/faq" element={<FAQ/> } />
        <Route path="/terms_and_conditions" element={<Terms/> } />
        <Route path="/cookies_policy" element={<Cookies/> } />
        <Route path="/value_for_fleet_management" element={<FirstPage />} />
        <Route path="/value_for_motor_insurers" element={<SecondPage />} />
        <Route path="/mobile_telematics_providers_are_commonly_seen_as_disruptive_innovators" element={<SolutionSection3 />} />
        <Route path="/insurance_companies_are_setting_for_a_profit_gain_from_the_mobile_telematics_systems" element={<SolutionSection4 />} />
        <Route path="/what_does_it_mean_to_drive_well_outside_of_any_emergency_situation?" element={<SolutionSection5 />} />
        <Route path="/connected_car" element={<SolutionSection6 />} />
        <Route path="/about_us" element={<Aboutus />} />
      </Routes>
      <Footer />
      <ScrollToTopButton/>
    </Router>
  );
}

export default App;
