import React from "react";
import Solutions from "./solutionsUI";
import background from "../../../assets/images/fleetBg2.png";
import Points from "./points";
import img1 from "../../../assets/images/i1.png"; // Import your images
import img2 from "../../../assets/images/i2.png"; // Import your images
import img3 from "../../../assets/images/i3.png";
import img4 from "../../../assets/images/i4.png";
import { Box, Grid, } from "@mui/material";
import banner from "../../../assets/images/image1.jpg";
import Banner from "../../components/banner/banner";
import SharedPagination from "./CustomPagination";
function FirstPage() {
  const pointsData = [
    {
      imgSrc: img1,
      title: "Realtime alerts",
      points: [
        "Alerts driver in real time with anticipation",
        "Cultivating safe driving behaviour culture",
      ],
    },
    {
      imgSrc: img2,
      title: "Social Responsibility",
      points: [
        "Improves images with customer  (less late delivery due to accident)",
        "Copes with corporate social responsibility",
        "Reduces accident rate",
      ],
    },
    {
      imgSrc: img3,
      title: "Reduce claims cost ",
      points: [
        "Access key details like driving risk alerts, anticipation, self confidence, vehicle impact location etc.",
        "Helps for a better negotiation with motor insurer",
      ],
    },
    {
      imgSrc: img4,
      title: "Improve your book of business",
      points: [
        "Improve your book by identifying, engaging, and rewarding safe drivers",
        "Reduce workers compensation costs and number of days away from work",
        "Reduces repair and replacement cost",
      ],
    },

    // Add more custom points data as needed
  ];

  return (
    <div style={{marginTop:"64px"}}>
      <div style={{ width: "100%" }}>
        <img
          src={banner}
          alt="banner"
          className="img-fluid"
          style={{
            width: "100%",
            height: "400px",
            objectFit: "cover",
          }}
        />
      </div>
      <div>
        <Banner title="" />
      </div>
      <Solutions
        title="Value for Fleet Management"
        paragraph=""
        imageSrc={background}
      />
      <Points pointsData={pointsData} /> {/* Pass the pointsData prop */}
     
      <div class='container' style={{marginTop:"0px",marginBottom:"50px"}}>
        <div class='row'>
          <div class='col-sm-3'></div>
          <div class='col-sm-6' style={{display:"flex",alignItems:"center",justifyContent:"center"}}>  <SharedPagination /> </div>
          <div class='col-sm-3'></div>
        </div>

      </div>

    </div>
  );
}

export default FirstPage;
