import React from "react";
import { Box, Container, Grid, Paper, Typography } from "@mui/material";
import RiskAlertNextImg_1 from "../../../../assets/images/aboutriskalertsnext.png"
import RiskAlertNextImg_2 from "../../../../assets/images/aboutriskalertsnext_2.png"
import RiskAlertNextImg_3 from "../../../../assets/images/aboutriskalertsnext_3.png"
import RiskAlertNextImg_4 from "../../../../assets/images/aboutriskalertsnext_4.png"
import RiskAlertNextImg_5 from "../../../../assets/images/aboutriskalertsnext_5.png"
import RiskAlertNextImg_6 from "../../../../assets/images/aboutriskalertsnext_6.png"
import RiskAlertNextImg_center from "../../../../assets/images/aboutriskalertsnext_center.png"
import './riskalert.css'
const data = [
  {
    imageSrc_1: RiskAlertNextImg_1,
    imageSrc_2: RiskAlertNextImg_2,
    imageSrc_3: RiskAlertNextImg_3,
    imageSrc_4: RiskAlertNextImg_4,
    imageSrc_5: RiskAlertNextImg_5,
    imageSrc_6: RiskAlertNextImg_6,
    imageSrc_Center: RiskAlertNextImg_center,
  },

];

const RiskAlertNext = () => {
  return (
    <div  style={{padding:"3rem 0 3rem 0"}} id="riskalert">
   <div style={{padding:"30px"}}>
    <Grid container   spacing={2} >
      {data.map((item, index) => (
        <Grid container spacing={2} key={index}>
          {/* First Item: Image 1 */}
          <Grid item xs={12} md={4} textAlign={"center"} display={'flex'} alignItems="center" className="alert-left">
            <Box >
              <img
                src={item.imageSrc_1}
                alt="Image 1"
                style={{
                  maxWidth: "80%", // Make the image responsive
                  height: "auto", // Maintain aspect ratio
                  marginBottom:"1rem"
                }}
              />
              <img
                src={item.imageSrc_2}
                alt="Image 2"
                style={{
                  maxWidth: "80%", // Make the image responsive
                  height: "auto", // Maintain aspect ratio
                  marginBottom:"1rem"
                }}
              />
              <img
                src={item.imageSrc_3}
                alt="Image 3"
                style={{
                  maxWidth: "80%", // Make the image responsive
                  height: "auto", // Maintain aspect ratio
                  marginBottom:"1rem"
                }}
              />
              
            </Box>
          </Grid>

          {/* Second Item: Text */}
          <Grid item xs={12} md={4} textAlign={"center"} display={'flex'} justifyContent={"center"} alignItems={"center"} flexDirection={"column"} className="risk-center">
            <Box >
            <img
                src={item.imageSrc_Center}
                alt="Image 3"
                style={{
                  maxWidth: "100%", // Make the image responsive
                  height: "auto", // Maintain aspect ratio
                  paddingLeft:"40px",
                  
                  
                }}
              />
            </Box>
          </Grid>

          {/* Third Item: Image 2 */}
          <Grid item xs={12} md={4} textAlign={"center"} sx={{display:{sm:'flex'},alignItems:{sm:"center"}}}>
            <Box >
            <img
            
                src={item.imageSrc_4}
                alt="Image 4"
                style={{
                  width: "60%", // Make the image responsive
                  height: "auto", // Maintain aspect ratio
                  marginBottom:"1rem"
                }}
              />
              <img
                src={item.imageSrc_5}
                alt="Image 5"
                style={{
                  width: "60%", // Make the image responsive
                  height: "auto", // Maintain aspect ratio
                  marginBottom:"1rem"
                }}
              />
              <img
                src={item.imageSrc_6}
                alt="Image 6"
                style={{
                  width: "60%", // Make the image responsive
                  height: "auto", // Maintain aspect ratio
                  marginBottom:"1rem"
                }}
              />
            </Box>
          </Grid>
        </Grid>
      ))}
    </Grid>
    </div>
    </div>
  );
};

export default RiskAlertNext;
