import React from "react";
import {
  Box,
  Grid,
  List,
  ListItem,
  ListItemText,
  Typography,
} from "@mui/material";

export default function PointsSection({imgSrc, title, points}) {
  return (
    <Grid xs={12} sm={6} md={6} lg={6} xl={6} sx={{width: "90%", padding: "2%",alignItems:"center"}} >
      <Box display={"flex"} alignItems={"center"} >
        <img src={imgSrc} alt="" style={{padding: "1%"}} />
        <Typography margin={"8px"} sx={{fontWeight: "bolder",fontSize:"20px",margin:"14px"}}>
          {title}
        </Typography>
      </Box>
      <div id="pointsection">
      <div className="line"></div>
      </div>
    
      <Box>
        <List
          sx={{
            listStyleType: "disc",
            listStylePosition: "inside",
            marginLeft: "2%",
          }}
        >
          {points.map((point, index) => (
            <ListItem sx={{padding: "0px"}} key={index} >
              <ListItemText >
                <span
                  style={{
                    position: "absolute",
                    left: "-0.8em",
                    fontSize: "15px",
                  }}
                >
                  ●
                </span>
               <span style={{fontSize:'18px',paddingLeft:'2px', lineHeight: "20px"}}> {point}</span>
              </ListItemText>
            </ListItem>
          ))}
        </List>
      </Box>
    </Grid>
  );
}
