import * as React from "react";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Typography from "@mui/material/Typography";
import { Button, CardActionArea, CardActions, Container, Paper, Box } from "@mui/material";
import { Grid } from "@mui/material";
import img1 from '../../assets/images/image1.jpg'
import img2 from '../../assets/images/image2.jpg'
import img3 from '../../assets/images/image5.jpg'
import img4 from '../../assets/images/image6.jpg'
import img5 from '../../assets/images/7d3620049c2e76c6d07fa3ea47d323c9.jpg'
import arrow from '../../assets/images/upper arrow.svg'
import { Link } from 'react-router-dom';
import { useEffect } from 'react';

const HomeSection4 = () => {

    const hoverStyles = {
        '&:hover': {
          backgroundColor: 'blue', // Change to whatever color you desire
          // Add any other styles for hover effect
        },
      };

    useEffect(() => {
        // Scroll to the top when the component mounts
        window.scrollTo(0, 0);

        // Optionally, you can also scroll to the top when a link is clicked
        const scrollToTop = () => {
            window.scrollTo(0, 0);
        };

        // Attach the scroll to top function to all links with class "scroll-to-top"
        const links = document.querySelectorAll(".scroll-to-top");
        links.forEach(link => {
            link.addEventListener("click", scrollToTop);
        });

        // Clean up event listeners when the component unmounts
        return () => {
            links.forEach(link => {
                link.removeEventListener("click", scrollToTop);
            });
        };
    }, []);



    const contentData = [
        {
            src: img1,
            text: "Value for Fleet Management",
            buttonText: "ALL",
            symbol: arrow,
            linkTo: "/solutions", // Add the link you want to redirect to
        },
        {
            src: img2,
            text: "Value for Motor Insurers",
            buttonText: "CASE STUDY",
            symbol: arrow,
            linkTo: "/value_for_motor_insurers", // Add the link you want to redirect to
        },
        {
            src: img3,
            text: "Connected Car",
            buttonText: "WHITE PAPER",
            symbol: arrow,
            linkTo: "/connected_car", // Add the link you want to redirect to
        },
        {
            src: img4,
            text: "What does it mean to drive well",
            buttonText: "BLOG",
            symbol: arrow,
            linkTo: "/what_does_it_mean_to_drive_well_outside_of_any_emergency_situation?", // Add the link you want to redirect to
        },
    ];


    return (
        <>
            <div id='section4' >
                <div class='container-fluid main'>

                    <div class='row sub1'>

                        <div class='col-sm-12 content1' style={{ display: 'flex', alignItems: "center", justifyContent: 'center' }}><div class='line'></div></div>
                        <div class='col-sm-12 content2' style={{ marginTop: "30px" }}><p>SOLUTIONS</p></div>

                    </div>
                    <div class='sub1' style={{ display: "flex", justifyContent: "center",padding:"10px" }}>


                        <Grid
                            container
                            spacing={{ xs: 3 }}
                            columns={{ xs: 3, sm: 8, md: 8, lg: 12 }}
                            style={{ display: "flex", justifyContent: "center", alignItems: "center", marginBottom: "50px" }}
                        >
                            {contentData.map((content, index) => (
                                <Grid item xs={3} sm={4} md={4} lg={3} key={index}>
                                    <Link to={content.linkTo} style={{ textDecoration: "none" }} className="scroll-to-top">
                                        <Card
                                            sx={{
                                                borderRadius: "10px",
                                                border: "1px solid #e0e0e0",
                                                boxShadow: "0px 4px 10px rgba(0, 0, 0, 0.2)",

                                            }}
                                        >


                                            <CardActionArea>
                                                <img
                                                    src={content.src}
                                                    alt={`Img ${index + 1}`}
                                                    style={{
                                                        height: "222px",
                                                        width: "100%",
                                                        objectFit: "cover",
                                                    }}
                                                    class='img-fluid'
                                                />

                                                <CardContent>
                                                    <CardActions
                                                        style={{
                                                            display: "flex",
                                                            justifyContent: "space-between",
                                                            padding: "0px",
                                                        }}
                                                    >
                                                        <Button
                                                            size="small"
                                                            sx={{
                                                                backgroundColor: "#FFCF24",
                                                                color: "#000000",
                                                                fontFamily: "segoe ui",
                                                                fontWeight: "600",
                                                                fontSize: "12px",

                                                                alignContent: "center",
                                                            }}
                                                        >
                                                            {content.buttonText}
                                                        </Button>
                                                        <p
                                                            style={{
                                                                fontFamily: "Segoe UI",
                                                                fontSize: "14px",
                                                                fontWeight: "400",
                                                                color: "#545252",
                                                                lineHeight: "28px",
                                                                alignContent: "center",
                                                            }}
                                                        >
                                                            {content.date}
                                                        </p>
                                                    </CardActions>
                                                    <Typography variant="body2" sx={{ color: "#000000", fontFamily: 'Segoe UI', fontSize: { md: "18px", lg: "15px" }, fontWeight: '600', marginTop: '10px' }}>
                                                        {content.text}
                                                    </Typography>








                                                </CardContent>
                                            </CardActionArea>



                                        </Card>

                                        {/* <Box
                                            sx={{

                                                border: '1px solid #e0e0e0',
                                                boxShadow: '0px 4px 10px rgba(0, 0, 0, 0.2)',
                                                marginTop: 2,

                                            }}
                                        >
                                            <Button
                                                variant="contained"
                                                disableElevation
                                                fullWidth
                                                className="lower"
                                                sx={{
                                                    display: 'flex',
                                                    justifyContent: 'space-between',
                                                    alignItems: 'center',
                                                    '&:hover': {
                                                        backgroundColor: 'lightgray', // Change to whatever color you desire
                                                        // Add any other styles for hover effect
                                                      },
                                                }}
                                            >
                                                {content.buttonText}
                                                <span>
                                                    <img src={content.symbol} alt="Symbol" style={{height: "30px"}}/>
                                                </span>
                                            </Button>
                                        </Box> */}

                                    </Link>
                                </Grid>
                            ))}
                        </Grid>
                    </div>


                    <div class='sub2  ' style={{padding:"10px"}}>
                        <div class='image-container'>
                            <img src={img5} width='100%' class='img-fluid' style={{ borderRadius: "20px", height: '450px', objectFit: "cover" }} />
                            <h1 class='image-heading'>“What does it mean to drive well, outside of any emergency situation?”</h1>
                        </div>
                    </div>






                </div>
            </div>
        </>
    );
}


export default HomeSection4